"use client";

import Image from "next/image";
import Link from "next/link";
import { useMemo, useState } from "react";

import { products, productImages } from "@/data/store";
import { useLanguage } from "@/context/LanguageContext";

function safeImg(src: any) {
  const s = String(src ?? "").trim();
  if (!s) return "/example.png";
  if (s.startsWith("/")) return s;
  if (s.startsWith("http://") || s.startsWith("https://")) return s;
  return "/example.png";
}

function norm(x: any) {
  return String(x ?? "").toLowerCase().trim();
}

function getPrimaryImageUrl(productId: number) {
  const primary = productImages.find((img: any) => img.product_id === productId && img.is_primary);
  const any = productImages.find((img: any) => img.product_id === productId);
  return primary?.url || any?.url || "/example.png";
}

export default function SearchPage() {
  const { lang } = useLanguage();
  const [q, setQ] = useState("");

  const results = useMemo(() => {
    const query = norm(q);
    if (!query) return [];

    return products.filter((p: any) => {
      const name = norm(p.name);
      const tags = Array.isArray(p.tags) ? p.tags.map(norm) : [];
      return name.includes(query) || tags.some((t: string) => t.includes(query));
    });
  }, [q]);

  return (
    <main className="min-h-screen bg-white text-black pb-24">
      <section className="mx-auto max-w-md px-4 pt-4">
        <div className="leading-tight mb-3">
          <div className="text-lg font-extrabold">{lang === "en" ? "Search" : "Raadi"}</div>
          <div className="text-xs text-gray-500">{lang === "en" ? "Raadi alaab" : "Search products"}</div>
        </div>

        <input
          value={q}
          onChange={(e) => setQ(e.target.value)}
          placeholder={lang === "en" ? "Type to search..." : "Qor si aad u raadiso..."}
          className="w-full h-11 rounded-xl border px-4 text-sm"
        />

        <div className="mt-4 grid grid-cols-2 gap-3">
          {results.map((p: any) => {
            const imgUrl = safeImg(getPrimaryImageUrl(p.id));
            const pname = lang === "en" ? p.name_en ?? p.name : p.name_so ?? p.name;

            return (
              <Link
                key={p.id}
                href={`/product/${p.slug}`}
                className="bg-white rounded-2xl border shadow-sm overflow-hidden"
              >
                <div className="p-2">
                  <Image
                    src={imgUrl}
                    alt={pname}
                    width={220}
                    height={220}
                    className="mx-auto h-28 w-auto object-contain"
                  />
                </div>
                <div className="px-3 pb-3">
                  <div className="text-[12px] line-clamp-2 min-h-[34px]">{pname}</div>
                </div>
              </Link>
            );
          })}

          {q && results.length === 0 && (
            <div className="col-span-2 text-sm text-gray-600 border rounded-2xl p-4">
              {lang === "en" ? "No results found." : "Natiijo lama helin."}
            </div>
          )}
        </div>
      </section>
    </main>
  );
}
